import React from "react";
import uuidv from "uuid/v4";
import * as d3 from "d3";
import '../scss/bar-chart-d3.scss'

class BarChartD3 extends React.Component {
    constructor(props) {
        super(props)
        this.id = '_' + uuidv().replace(/-/g, '')
        this.getId = this.getId.bind(this)
        this.setId = this.setId.bind(this)
        this.state = {
            company: {},
            industry:{}
        }
    }

    getId(id) {
        return '#' + this.id + '-' + id;
    }

    setId(id) {
        return this.id + '-' + id;
    }

    build() {
        let width = 360;
        let data = [
            {value: -8, name: "شرکت", color_bar: "#2394DF", color_name: "white"},
            {value: 13, name: "صنعت", color_bar: "#71E7D6", color_name: "black"},
            // {value: 25, name: "example 3", color_bar: "#BB4786", color_name: "white"}
        ];
        let height = 216;
        let bar_width = width / 3;
        let scale_factor = 5;

        let svg = d3.select(this.getId("svg"))
            .attr("width", width)
            .attr("height", height)

        svg.empty()
        svg.selectAll("*").remove();

        let bar = svg.selectAll("g")
            .data(data)
            .enter()
            .append("svg")
            .attr("x", function (d, i) {
                return bar_width * i
            })
            .attr("y", 0)
            .attr("width", bar_width)

        bar = bar.append("g")
            .attr("transform", function (d, i) {
                let translateY = (height - (d.value) * scale_factor)
                return "translate(0," + translateY + ")"
            })

        bar.append("rect")
            .attr("x", 0)
            .attr("y", 0)
            .attr("width", bar_width)
            .attr("height", function (d) {
                return (d.value * scale_factor)
            })
            .attr("fill", function (d) {
                return d.color_bar
            })
        let svg_name = bar.append("svg")
            .attr("x", 8)
            .attr("y", 18)
            .attr("class", "svg_name")

        svg_name
            .append("text")
            .attr("class", "bar_name")
            .append("tspan")
            .attr("x", 0)
            .text(function (d, i) {
                return d.name;
            })
            .attr("fill", function (d, i) {
                return d.color_name
            })

        let svg_percentage = bar.append("svg")
            .attr("x", 8)
            .attr("y", -8)
            .attr("class", "svg_percentage")
        svg_percentage
            .append("text")
            .attr("class", "bar_percentage")
            .append("tspan")
            .attr("x", 0)
            .text(function (d, i) {
                return d.value;
            })

        //xAxis
        svg.append("rect")
            .attr("x", 0)
            .attr("y", height)
            .attr("width", width)
            .attr("height", 1)
            .attr("fill", "white")
    }

    componentDidMount() {
        console.log(this.props.data);
        // if (this.props.data.ticker) {
        //     httpService.httpGet(`stocks/financialStats?items=grNIL3y&tickers=${this.props.data.ticker}`).then(res => {
        //         if (res && res.isSuccess) {
        //             this.setState({company:res.data})
        //         }
        //     })
        //     httpService.httpGet(`stocks/financialStats?items=grNIL3y&tickers=${this.props.data.ticker}&tickerTypes=industryIndex&exchanges=IREX`).then(res => {
        //         if (res && res.isSuccess) {
        //             this.setState({industry:res.data})
        //         }
        //     })
        // }

        this.build()
    }

    render() {
        return (
            <div id={this.setId("svg-container")} align={"center"}>
                <svg id={this.setId("svg")} className={"svg"}>

                </svg>
            </div>
        );
    }
}

export default BarChartD3
