import React from "react";
import { Chart } from "react-google-charts";
import './chart.scss'
const data = [
    ["", "Density", { role: "style" }],
    ["", -40, "#b87333"], // RGB value
    ["", 10.49, "silver"], // English color name
    ["", 19.3, "gold"],
    ["", 21.45, "color: #e5e4e2"] // CSS-style declaration
];

const options = {
    backgroundColor:'transparent',
    legend: "none"
};
export default class NewBarChart extends React.Component {
    render() {
        return (
            <div>
                <Chart
                    chartType="ColumnChart"
                    width="100%"
                    height="400px"
                    data={data}
                    options={options}
                />
            </div>
        );
    }
}
